}

.btn--tiny {
  cursor: pointer;
  color: #ff887c;
  background: none;
  border: 1px solid #ff887c;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  font-size: large;
  display: flex;
}

.recipe__ingredients {
  background-color: #f2efee;
  flex-direction: column;
  align-items: center;
  padding: 5rem 8rem;
