.pagination {
  margin-top: auto;
  padding: 0 3.5rem;
}

.pagination__btn--next {
  float: right;
}

.btn--inline {
  color: #f38e82;
  cursor: pointer;
  background-color: #f9f5f3;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: .8rem 1.2rem;
